@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    font-family: 'Roboto', sans-serif;
    color: white;
}

body {
    background-color: #1A1A1A;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container_center {
}

.container__chart {
    max-width: 800px;
    margin: 0 auto;
}

.header__title {
    text-align: center;
}

.header__text {
    text-align: center;
}

.calc {
    display: flex;
    justify-content: center;
}

.form {
    display: flex;
}

.form__input {
    font-size: 20px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 300px;
    color: white;
    border: 3px solid #292929;
    background-color: #1F1F1F;
}

.form__select {
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 80px;
    border: 3px solid #292929;
    border-left: none;
    background-color: #1F1F1F;
}

.form__input, .form__select {
    box-sizing: border-box;
}

.form__input:focus { 
    outline:none; 
}

.form__input_number::-webkit-outer-spin-button, .form__input_number::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.swap {
    padding-left: 20px;
    padding-right: 20px;
}

.swap__symbol {
    font-size: 35px;
}
